<!-- src/components/ThemeToggle.vue -->
<template>
  <div :class="['toggle-wrapper', { 'dark-mode': isDarkMode }]" @click="toggleTheme">
    <div class="toggle-circle">
      <i :class="['icon', isDarkMode ? 'icon-moon' : 'icon-sun']"></i>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isDarkMode: false,
    };
  },
  methods: {
    toggleTheme() {
      this.isDarkMode = !this.isDarkMode;
      document.documentElement.setAttribute('data-theme', this.isDarkMode ? 'dark' : 'light');
    },
  },
};
</script>

<style scoped>
.toggle-wrapper {
  width: 50px;
  height: 24px;
  background-color: #f0f0f0;
  border-radius: 12px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 2px;
  transition: background-color 0.3s;
}

.toggle-wrapper.dark-mode {
  background-color: #4b4b4b;
}

.toggle-circle {
  width: 20px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s;
}

.toggle-wrapper.dark-mode .toggle-circle {
  transform: translateX(26px);
}

.icon {
  font-size: 12px;
}

.icon-sun::before {
  content: '☀️';
}

.icon-moon::before {
  content: '🌙';
}
</style>
