<template>
  <div class="LoginFormss">
    <div class="login-container">
      <form class="login-form" @submit.prevent="submitForm">
        <h2>Dastur sahifasiga kirish</h2>
        <p>Xodim tomonidan berilgan login va parol orqali dasturga kirib va undan foydalanishingiz mumkin</p>
        <label for="login">Loginni kiriting</label>
        <input type="text" id="login" v-model="login" placeholder="Telefon raqam yoki maxsus nom" required>
        <label for="password">Parolni kiriting</label>
        <div class="password-container">
          <input :type="passwordFieldType" id="password" v-model="password" placeholder="Parolni kiriting" required>
          <button type="button" class="toggle-password" @click="togglePasswordVisibility">
            {{ passwordFieldType === 'password' ? '👁️' : '🙈' }}
          </button>
        </div>
        <button type="submit">Dasturga kirish</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'LoginForm',
  data() {
    return {
      login: '',
      password: '',
      passwordFieldType: 'password'
    };
  },
  methods: {
    submitForm() {
      axios.post(`https://api-kasb.maktab.uz/api/v1/admin/login/`,{
        'email':this.login,
        'password':this.password
      })
          .then(res=>{
            localStorage.setItem('token',res.data.access)
            axios.get(`https://api-kasb.maktab.uz/api/v1/students/me/`,
                {
                  headers: {
                    'Authorization': `Bearer ${res.data.access}`,
                    'Accept-Language':'oz'
                  }
                }
            )
                .then(()=>{
                  this.$router.push(`/`)
                })
            // this.$router.push('/')
          })
          .catch(()=>{

          })
      // Handle form submission
      console.log('Login:', this.login);
      console.log('Password:', this.password);
    },
    togglePasswordVisibility() {
      this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password';
    }
  }
};
</script>

<style scoped>
body {
  font-family: "Cera Pro";
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.login-container {
  background-color: #ffffff;
  padding: 20px 40px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 400px;
  max-width: 90%;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-form h2 {
  margin-bottom: 10px;
  font-size: 24px;
  color: #333333;
  text-align: center;
}

.login-form p {
  text-align: center;
  font-size: 14px;
  color: #666666;
  margin-bottom: 20px;
}

.login-form label {
  font-size: 14px;
  color: #333333;
  margin-bottom: 5px;
}

.password-container {
  position: relative;
  display: flex;
  align-items: center;
}

.login-form input {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  font-size: 14px;
  flex: 1;
}

.toggle-password {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

.login-form .forgot-password {
  align-self: flex-end;
  font-size: 12px;
  color: #007BFF;
  text-decoration: none;
  margin-bottom: 20px;
}

.login-form .forgot-password:hover {
  text-decoration: underline;
}

.login-form button[type="submit"] {
  padding: 10px;
  background-color: #007BFF;
  border: none;
  border-radius: 4px;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

.login-form button[type="submit"]:hover {
  background-color: #0056b3;
}
.LoginFormss{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
</style>
