<template>
  <div>
    <div class="charts_title">
      11-sinf
    </div>
    <div class="counts_charts">
      <div class="all_count">
        <div class="count_title">
          Jami o‘quvchilar
        </div>
        <div class="count_desc">
          593 493
        </div>
      </div>
      <div class="quiz_count">
        <div class="count_title">
          So‘rovda qatnashganlar
        </div>
        <div class="count_desc">
          {{series[1].data[0]}}
        </div>
      </div>
    </div>
    <div class="charts_fathers">
      <apexchart type="bar" height="150" :options="chartOptions" :series="series"></apexchart>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import VueApexCharts from 'vue3-apexcharts';

export default {
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: [
        {
          name: 'Jami o\'quvchilar',
          data: [] // Initially empty, will be populated by API data
        },
        {
          name: 'So\'rovda qatnashganlar',
          data: [] // Initially empty, will be populated by API data
        }
      ],
      chartOptions: {
        chart: {
          type: 'bar',
          height: 550
        },
        plotOptions: {
          bar: {
            horizontal: true,
          }
        },
        dataLabels: {
          enabled: false
        },
        xaxis: {
          categories: [''] // Single category for horizontal bar chart
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['transparent']
        },
        colors: ['#69D2E7', '#58B19F'],
        legend: {
          position: 'bottom'
        },
        tooltip: {
          shared: true,
          intersect: false
        },
      }
    };
  },
  methods: {
    async fetchChartData() {
      try {
        const response = await axios.get('https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers1/?class_id=9', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        });

        // Assuming response.data contains the required statistics
        const totalStudents = 593493; // Replace with actual data if needed
        const participated = response.data.total;

        // Update the series data
        this.series[0].data = [totalStudents];
        this.series[1].data = [participated];

      } catch (error) {
        console.error('Error fetching chart data:', error);
      }
    }
  },
  mounted() {
    this.fetchChartData();
  }
};
</script>

<style>
.charts_fathers {
  width: 100%;
}
</style>
